@use "global" as *;

.branch-thanks__inner {
  position: relative;
}

.branch-thanks__inner.inner {
  max-width: rem(600);

  @include mq(md) {
    max-width: calc(rem(700) + $padding-pc * 2);
  }
}
.branch-thanks__inner::after {
  content: "";
  background: url(../images/common/goldfish-logo.svg);
  width: rem(120);
  left: calc(50% - min(46vw, rem(635)));
  top: rem(-25);
  position: absolute;
  z-index: -1;
  aspect-ratio: 120 / 55;
  background-size: contain;
  @include mq("md") {
    top: rem(-70);
    display: block;
    aspect-ratio: 301 / 138;
    width: rem(301);
    left: calc(50% - min(43vw, rem(635)));
  }
}

.branch-thanks__text {
  text-align: center;
}
.branch-thanks__text:nth-child(2) {
  margin-top: rem(40);
}
