@use "global" as *;

.campaign-card {
  box-shadow: rem(2) rem(2) rem(10) 0 #00000040;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.campaign-card__img img {
  aspect-ratio: 280 / 188;
  object-fit: cover;
}

.campaign-card__img.campaign-card__img--sidebar img {
  aspect-ratio: 294 / 188;
}

.campaign-card__body {
  padding: rem(16) rem(16) rem(24);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  @include mq("md") {
    padding: rem(24) rem(16) rem(40);
  }
}

.campaign-card__body.campaign-card__body--branch {
  @include mq("md") {
    padding: rem(24) rem(24) rem(50);
  }
}

.campaign-card__body.campaign-card__body--sidebar {
  @include mq("md") {
    padding: rem(16) rem(23) rem(20);
  }
}

.campaign-card__top {
  padding-bottom: rem(4);
  border-bottom: rem(1) solid $green;
  text-align: left;
  flex-grow: 1;
}

.campaign-card__tag {
  font-size: rem(12);
  line-height: 1.83;
  color: $green;
  border: rem(1) solid $green;
  padding: rem(4) rem(12);
  display: inline-block;
  @include mq("md") {
    padding: rem(6) rem(16);
  }
}

.campaign-card__title {
  font-size: rem(16);
  font-weight: 500;
  line-height: 1.5;
  margin-top: rem(4);
  @include mq("md") {
    margin-top: rem(11);
  }
}

.campaign-card__title.campaign-card__title--branch {
  @include mq("md") {
    font-size: rem(24);
    margin-top: rem(4);
  }
}

.campaign-card__title.campaign-card__title--sidebar {
  text-align: center;
  font-size: rem(16);
  font-weight: 500;
  margin-top: 0;
}

.campaign-card__bottom {
  width: 100%;
  text-align: center;
  padding: rem(8) 0 0;
  @include mq("md") {
    padding: rem(18) rem(20) 0;
  }
}

.campaign-card__bottom.campaign-card__bottom--sidebar {
  @include mq("md") {
    padding: rem(10) rem(20) 0;
  }
}

.campaign-card__text {
  display: inline-block;
  font-size: rem(14);
  line-height: 1.7414;
  @include mq("md") {
    font-size: rem(16);
  }
}

.campaign-card__text.campaign-card__text--sidebar {
  @include mq("md") {
    font-size: rem(14);
  }
}

.campaign-card__price {
  display: flex;
  justify-content: center;
  margin-top: rem(8);
  align-items: baseline;
}

.campaign-card__price-normal {
  font-family: $Lato;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1;
  @include mq("md") {
    font-size: rem(24);
  }
}

.campaign-card__price-normal.campaign-card__price-normal--sidebar {
  @include mq("md") {
    font-size: rem(20);
  }
}

.campaign-card__price-normal span {
  text-decoration: line-through;
  text-decoration-color: red;
}

.campaign-card__price-discount {
  font-family: $Lato;
  font-size: rem(32);
  font-weight: $bold;
  line-height: rem(32);
  margin-left: rem(16);
  color: $green;
  @include mq("md") {
    font-size: rem(40);
    margin-left: rem(24);
  }
}

.campaign-card__price-discount.campaign-card__price-discount--sidebar {
  @include mq("md") {
    font-size: rem(32);
  }
}

.campaign-card__explain {
  font-size: rem(16);
  margin-top: rem(24);
  line-height: 1.5;
  text-align: left;
}

.campaign-card__button-wrapper {
  text-align: center;
}

.campaign-card__date {
  display: inline-block;
  font-family: $Lato;
  color: $green;
  font-size: rem(16);
  line-height: 1.125;
  font-weight: bold;
  @include mq("md") {
    margin-top: rem(32);
  }
}

.campaign-card__button-message {
  color: $green;
  font-size: rem(14);
  line-height: 1.286;
  margin-top: rem(7);
}

.campaign-card__button {
  @include mq("md") {
    margin-top: rem(16);
  }
}
